import React from 'react'
import './App.css'

function App() {
  const  preview_image = (event)  => {
    var reader = new FileReader();
    reader.onload = function()
    {
      var output = document.getElementById('input_image');
      output.src = reader.result;
      fetch("/result", {
        method:"POST",
        cache: "no-cache",
        headers:{
            "Content-type":"application/json",
        },
        body:JSON.stringify(reader.result)
        }
      )
      .then(response => {
        return console.log(response.body)
      })  
      .catch(err => {
        console.log(err)
      })
    }
    reader.readAsDataURL(event.target.files[0]);
  }
  return (
    <div class="main-container">
        <div class="output-box">
            <img id="" alt=""/>
        </div>
        <div class="wrapper">
            <img id="input_image"/>
            <div class="btn-container">
                <label class="custom-file-upload btn upload" >
                    <input type="file" onChange={preview_image} />
                    Upload
                </label>
                <input type="button" class="btn fil" value="Filter"/>
            </div>
        </div>
    </div>
  );
}

export default App;
